@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Space+Grotesk&display=swap);
body {
  overflow: hidden;
  margin: 0;
  padding: 0;
  font-family: 'Space Grotesk', sans-serif;
  height: 100vh;
  background: linear-gradient(to top, rgba(197, 114, 75, 0.932), rgb(124, 41, 62));
}

h1 {
  font-family: 'Lobster', cursive;
  color: whitesmoke;
  border-bottom: 3px solid rgba(0, 0, 0, 0.26);
}

.background{
  background: url(/face-box/static/media/pattern.3866732b.png);
  opacity: 0.1;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

nav {
  display: flex;
  justify-content: flex-end;
}

.sign-out { 
  background-color: rgba(0, 0, 0, 0.103);
  color: white;
  border: 1px solid rgba(0, 0, 0, 0.123);
  margin-right: 15px;
}

.sign-out:hover {
  background-color: rgba(0, 0, 0, 0.322);
}
.Tilt {
  background: linear-gradient(to top, rgba(255, 147, 97, 0.199), rgba(78, 21, 35, 0.199));
  border: 2px solid rgba(0, 0, 0, 0.26);
}
.light-red {
  text-transform: capitalize;
  font-weight: 800;
  font-size: 2em;
}
.form {
    width: 700px;
    background-color: rgba(0, 0, 0, 0.281);
}
.face {
  border-radius: 3%;
}

.bounding-box {
  position: absolute;
  box-shadow: 0 0 0 3px #fc6f56;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}
.App {
  text-align: center;
}

.center {
    display: flex;
    justify-content: center;
}

.particles {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

